import React from 'react'
import Waypoint from 'react-waypoint'
import scrollToComponent from 'react-scroll-to-component';
import './intelligent-section.scss'
import ProductRange from '-!babel!svg-react-loader?name=Icon!./aw1.svg';
import Checked from '-!babel!svg-react-loader?name=Icon!./checked.svg';
import Warning from '-!babel!svg-react-loader?name=Icon!./warning.svg';
import Cancel from '-!babel!svg-react-loader?name=Icon!./cancel.svg';
import image from './PNG/approval-workflow.png'

class DeliverySection extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            sectionOneEnter: false,
            sectionTwoEnter: false,
            sectionThreeEnter: false,
        }
        this._handleWaypointEnter = this._handleWaypointEnter.bind(this);
        this._handleWaypointLeave = this._handleWaypointLeave.bind(this);
    }
    _handleWaypointEnter(){
        console.log(this.state.sectionOneEnter);
        scrollToComponent(this.view, { offset: 0, align: 'top', duration: 1000});
        this.setState({
            sectionOneEnter: true
        })
    }
    _handleWaypointLeave(){
        console.log('viewed..!');
        this.setState({
            sectionOneEnter: false
        })
    }
    render(){
        return(
        <Waypoint onEnter={this._handleWaypointEnter} onLeave={this._handleWaypointLeave}>
            <div className={"section intelligent-section" + (this.state.sectionOneEnter ? " fade-in" : " fade-out")} ref={(section) => { this.view = section; }}>
                <div className="container">
                    <h1 className="section-title">Implementing our <span>Centre-led Approval System</span> helped URBAN LADDER gain 80% more visibility into their procurement process</h1>
                    <div className="row">
                        <div className="col-5">
                            <p className="section-lead">Our real-time approval workflow empowers enterprises to have more control on purchase requisitions. It reduces all human errors to save precious time and resources; thereby, improving efficiency more easily.</p>
                        </div>
                        <div className="col-10">
                            <Notification />
                        </div>
                    </div>
                </div>
            </div>
        </Waypoint>
        )
    }
}

export default DeliverySection;

export class Notification extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            items: [1,2,3,4],
        }
    }
    componentDidMount() {
    var i=5;
      this.interval = setInterval(function(){
        this.state.items.splice(0,0,i);
        this.setState({items:this.state.items.slice(0,4)});
        if(i==1){
                i=5;
            }
            else{
                i--;
            }
        // this.setState({items: this.state.items.splice(0,0,i)},function(){
        //     console.log("return");
        //     console.log(this.state.items);
        //     console.log(this.state.items.length);
        //     if(this.state.items.length == 3){
        //         this.setState({items: this.state.items.splice(0,2)},function(){
        //             console.log(this.state.items);
        //         })
        //     }
            
        //     console.log(this.state.items);
        // })
      }.bind(this), 2000);
    }
    componentWillUnmount() {
      clearInterval(this.interval);
      // clearTimeout(this.interval);
    }

    message(number){
        
        if(number == 1){
            return "Order #0001 placed for Bangalore";
        }
        if(number == 2){
            return "Order #0002 is beyond PO value";
        }
        if(number == 3){
            return " Order #0003 is awaiting approval";
        }
        if(number == 4){
            return "Order #0010 placed for Bangalore";
        }
        if(number == 5){
            return "Order #0012 is beyond PO value";
        }
    }

    icons(items)
    {
        console.log('number');
        console.log(items);
        if(items ==1){
            console.log("im here");
            return <Checked />
        }
        if(items ==2){
            return <Cancel />
        }
        if(items ==3){
            return <Warning />
        }
        if(items ==4){
            return <Checked />
        }
        if(items ==5){
            return <Cancel />
        }
    }
    render(){
        let items = this.state.items
        return(
            <div className="NotificationSection">
                <div className="notification-container">
                   {items.map(item => 
                        <div className="notification" key={item} >
                            <div className="icons">{this.icons(item)}</div>
                            <div className="contents">{this.message(item)}</div>
                        </div>               
                    )} 
               </div>
           </div>
        )
    }
}


